import { Component, OnInit } from '@angular/core';
import { AdminLayoutService } from '../../admin-layout.service';
import { SettingService } from 'src/app/services/setting.service';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { ToastrService } from 'ngx-toastr';
import { NgForm } from '@angular/forms';

@Component({
  selector: 'app-category',
  templateUrl: './category.component.html',
  styleUrls: ['./category.component.css']
})
export class CategoryComponent implements OnInit {

  creatingRole = false;

  category = {
    'title': '',
    'description': ''
  };

  updateCategory: any;

  categories: any;

  constructor(
    private adminLayoutService: AdminLayoutService,
    private modalService: NgbModal,
    private settingsService: SettingService,
    private toastrService: ToastrService
  ) { }


  ngOnInit(): void {
    this.adminLayoutService.setPageTitle('Categories');
    this.getCategories();
  }

  open(content, category: any = null) {
    if (category !== null){
      this.updateCategory = category;
    }
    this.modalService.open(content, { ariaLabelledBy: 'modal-basic-title' });
  }

  onSubmit(form: NgForm) {
    this.settingsService.createCategory(this.category).subscribe(
      data => {
        if (data.status === 'success') {
          this.getCategories();
          this.toastrService.success(data.message);
          form.form.reset();
        } else {
          this.toastrService.error(data.message);
        }
      }
    );
  }

  updateCate() {
    this.settingsService.updateCategory(this.updateCategory).subscribe(
      data => {
        if (data.status === 'success') {
          this.getCategories();
          this.toastrService.success(data.message);
        } else {
          this.toastrService.error(data.message);
        }
      }
    );

  }

  getCategories() {
    this.settingsService.getCategories().subscribe(
      data => {
        this.categories = data;
      }
    );
  }

  deleteCategory(id) {
    if (confirm('Are you sure?')) {

      this.settingsService.deleteCategery(id).subscribe(
        data => {
          if (data.status === 'success') {
            this.getCategories();
            this.toastrService.success(data.message);
          } else {
            this.toastrService.error(data.message);
          }
        }
      );
    }
  }

}
