import { Component, OnInit } from '@angular/core';
import { AdminLayoutService } from '../../admin-layout.service';
import { UserService } from 'src/app/services/user.service';

@Component({
  selector: 'app-pins',
  templateUrl: './pins.component.html',
  styleUrls: ['./pins.component.css']
})
export class PinsComponent implements OnInit {


  pins: any;

  constructor(
    private adminLayoutService: AdminLayoutService,
    private userService: UserService
  ) { }

  ngOnInit(): void {
    this.adminLayoutService.setPageTitle('Vendor Pins');
    this.getPins();
  }


  getPins() {
    this.userService.getPins().subscribe(
      data => {
        this.pins = data;
        console.log(data);
      }
    );
  }

  hidePin(pin: string, length: number) {
    let result = '';
    for (let x = 0; x < pin.length; x++) {
      result += x < length || x >= pin.length - length  || pin[x] === '-' ? pin[x] : 'x';
    }
    return result;
  }


}
