import { Component, OnInit } from '@angular/core';
import { AdminLayoutService } from '../../admin-layout.service';
import { NgbModal, NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { UserService } from 'src/app/services/user.service';
import { NgForm } from '@angular/forms';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-administrators',
  templateUrl: './administrators.component.html',
  styleUrls: ['./administrators.component.css']
})
export class AdministratorsComponent implements OnInit {

  createAdmin = {
    'email': '',
    'first_name': '',
    'last_name': '',
    'password': ''
  };

  admin = { ...this.createAdmin };

  administrators: any;

  constructor(
    private adminLayoutService: AdminLayoutService,
    private modalService: NgbModal,
    private userService: UserService,
    private toastrService: ToastrService
  ) { }

  ngOnInit(): void {
    this.adminLayoutService.setPageTitle('Admininstrator List');
    this.getAdministrators();
  }

  open(content) {
    this.modalService.open(content, { ariaLabelledBy: 'modal-basic-title' });
  }

  onSubmit(form: NgForm, model: NgbActiveModal) {
    this.userService.createAdministrator(this.admin).subscribe(
      data => {
        this.getAdministrators();
        if (data.status === 'success') {
          this.toastrService.success(data.message);
          form.form.reset();
          model.close();
        } else {
          this.toastrService.error(data.message);
        }
      }
    );

  }

  getAdministrators() {
    this.userService.getAdministrators().subscribe(
      data => {
        this.administrators = data;
        console.log(data);
      }
    );
  }

}
