import { Component, OnInit } from '@angular/core';
import { SettingService } from 'src/app/services/setting.service';
import { AdminLayoutService } from '../../admin-layout.service';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-settings',
  templateUrl: './settings.component.html',
  styleUrls: ['./settings.component.css']
})
export class SettingsComponent implements OnInit {

  settings = {
    comment: 50,
    id: 1,
    login_bonus: 200,
    sign_up_bonus: 3000,
    max_withdrawal_limit: 5000,
    min_withdrawal_limit: 3000,
    read_news: 50,
    referral_bonus: 1000,
    share_daily_advert: 200,
    share_daily_sponsored: 200,
    share_news: 50,
    sign_up_fee: 3000,
    testimonies_bonus: 500
  };

  constructor(
    private adminLayoutService: AdminLayoutService,
    private toastrService: ToastrService,
    private settingService: SettingService
  ) { }

  ngOnInit(): void {
    this.adminLayoutService.setPageTitle('Site Settings');
    this.getSettings();
  }

  getSettings() {
    this.settingService.getSettings().subscribe(
      data => {
        console.log(data);
        this.settings = data.data;
      }
    );
  }

  onSubmit() {
    this.settingService.updateSettings(this.settings).subscribe(
      data => {
        this.toastrService.success(data.message);
        this.settings = data.data;
      }
    );

  }
}
