import { Component, OnInit } from '@angular/core';
import { AdminLayoutService } from '../../admin-layout.service';
import { ToastrService } from 'ngx-toastr';
import { SettingService } from 'src/app/services/setting.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-new-post',
  templateUrl: './new-post.component.html',
  styleUrls: ['./new-post.component.css']
})
export class NewPostComponent implements OnInit {

  categories: any;

  post = {
    title: '',
    link: '',
    abstract: '',
    content: '',
    category_id: '',
    is_sponsored: false,
    img: {
      type: '',
      src: ''
    }
  };

  
  uploadImageTag: any;

  constructor(
    private adminLayoutService: AdminLayoutService,
    private toastrService: ToastrService,
    private settingService: SettingService,
    private router: Router
  ) { }

  ngOnInit(): void {
    this.adminLayoutService.setPageTitle('Create New Post');
    this.getCategories();
  }

  onSubmit() {
    this.settingService.createPost(this.post).subscribe(
      data => {
        if (data.status === 'success') {
          this.toastrService.success(data.message);
          this.router.navigate(['/admin/post', data.data.id])
        } else {
          this.toastrService.error(data.message);
        }
      }
    );
  }

  getCategories() {
    this.settingService.getCategories().subscribe(
      data => {
        this.categories = data;
      }
    );
  }


  uploadImageInput(event: any) {
    const reader = new FileReader();
    const file = event.target.files[0];
    let ext = file.name.split('.');
    ext = ext[ext.length - 1];

    reader.onloadend = () => {
        const img = reader.result.toString().split(',')[1];
        this.uploadImageTag = reader.result;
        this.post.img = {
            type: ext,
            src: img
        };
    };
    reader.readAsDataURL(file);
  }
}
