import { Component, OnInit } from '@angular/core';
import { AdminLayoutService } from '../../admin-layout.service';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { UserService } from 'src/app/services/user.service';
import { ToastrService } from 'ngx-toastr';
import { NgForm } from '@angular/forms';

@Component({
  selector: 'app-vendor',
  templateUrl: './vendor.component.html',
  styleUrls: ['./vendor.component.css']
})
export class VendorComponent implements OnInit {

  creatingAdmin = false;

  selectedVendor: any;
  
  vendorPin = {
    vendor_id: 0,
    amount: 0
  };

  vendor = {
    'email': '',
    'first_name': '',
    'last_name': '',
    'password': '',
    'phone': ''
  };

  vendors: any;

  constructor(
    private adminLayoutService: AdminLayoutService,
    private modalService: NgbModal,
    private userService: UserService,
    private toastrService: ToastrService
  ) { }

  ngOnInit(): void {
    this.adminLayoutService.setPageTitle('Admininstrator List');
    this.getVendors();
  }

  open(content) {
    this.modalService.open(content, { ariaLabelledBy: 'modal-basic-title' });
  }

  openPins(content, vendor) {
    this.selectedVendor = vendor;
    this.modalService.open(content, { ariaLabelledBy: 'modal-basic-title' });
  }

  generatePin(form: NgForm) {
    this.vendorPin.vendor_id = this.selectedVendor.id;
    this.userService.generatePins(this.vendorPin).subscribe(
      data => {
        if (data.status === 'success') {
          const vendorId = this.vendors.data.indexOf(this.selectedVendor);
          this.selectedVendor = data.data;
          this.vendors.data[vendorId] = this.selectedVendor;
          this.toastrService.success(data.message);
          form.form.reset();
        } else {
          this.toastrService.error(data.message);
        }
      }
    );
  }

  onSubmit(form: NgForm) {
    this.userService.createVendor(this.vendor).subscribe(
      data => {
        this.getVendors();
        if (data.status === 'success') {
          this.toastrService.success(data.message);
          form.form.reset();
        } else {
          this.toastrService.error(data.message);
        }
      }
    );

  }

  getVendors() {
    this.userService.getVendors().subscribe(
      data => {
        this.vendors = data;
        console.log(data);
      }
    );
  }



}
