import { Component, OnInit } from '@angular/core';
import { AdminLayoutService } from '../../admin-layout.service';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { UserService } from 'src/app/services/user.service';
import { ToastrService } from 'ngx-toastr';
import { NgForm } from '@angular/forms';

@Component({
  selector: 'app-roles',
  templateUrl: './roles.component.html',
  styleUrls: ['./roles.component.css']
})
export class RolesComponent implements OnInit {

  creatingRole = false;

  role = {
    'title': '',
    'description': ''
  };

  roles: any;

  constructor(
    private adminLayoutService: AdminLayoutService,
    private modalService: NgbModal,
    private userService: UserService,
    private toastrService: ToastrService
    ) { }


  ngOnInit(): void {
    this.adminLayoutService.setPageTitle('Admininstrator Roles');
    this.getAdministratorRoles();
  }

  open(content) {
    this.modalService.open(content, { ariaLabelledBy: 'modal-basic-title' });
  }

  onSubmit(form: NgForm) {
    this.creatingRole = true;
    this.userService.createAdministratorRole(this.role).subscribe(
      data => {
        this.creatingRole = false;
        this.getAdministratorRoles();
        if (data.status === 'success') {
          this.toastrService.success(data.message);
          form.form.reset();
        } else {
          this.toastrService.error(data.message);
        }
      }
    );

  }

  getAdministratorRoles() {
    this.userService.getAdministratorRoles().subscribe(
      data => {
        this.roles = data;
        console.log(data);
      }
    );
  }

}
